<template>
    <div class="Acripts">
        <Carousel :type="'acripts'" :num="6"></Carousel>
        <div class="img_list">
            <div class="img_list_top">
                <span>名人字画</span>
            </div>
            <div class="img_box clearfloat">
                <div class="img_div" v-for="(car,index) in list" :key="index"  @click="goPage(index+1)">
                  <div class="car_img">
                        <img  :src="car.img">
                  </div>
                  <div class="text-bottom">{{car.name}}</div>
                </div>
            </div>
        </div>
  </div>
</template>
<script>
import Carousel from '@/components/Carousel'
export default {
  name: 'Acripts',
  components: {
    Carousel
  },
  data () {
    return {
        list:[]
    }
  },
  methods:{
	  pushList(){
          var name = ['《齐白石像》范曾',
          '《蚕》齐白石',
          '《栗子》齐白石','《清风》齐白石','《松树葡萄》齐白石',
          '齐白石草虫圆光（一）','齐白石草虫圆光（二）','齐白石草虫圆光（三）',
          '齐白石草虫圆光（四）','《老莲堂》齐白石','《吉利图》许麟庐','《秋色秋声》许麟庐',
          '《桃鸠图》于非闇']
          for(var i = 0;i<name.length;i++){
              this.list.push(
                  {
                      img:require('../assets/acripts/'+(i+1)+'-y.jpg'),
                      name:name[i]
                  }
              )
          }
      },
      goPage(type){
           this.$router.push({path:'/AcriptsDetails',query:{id:type}});
      }
  },
  mounted(){
   this.pushList();
  }
}
</script>
<style lang="less" scoped>
    .img_list{
      width: 1170px;
      margin: 0 auto;
      cursor: pointer;
  }
  .img_list_top{
      padding: 12px 0;
      text-align: center;
      font-size: 20px;
    color: #79242F;
       border-top: 2px solid  #79242F;
    border-bottom: 2px solid  #79242F;
    margin-bottom: 20px;
     position: relative;
    .know{
        position: absolute;
        right: 0;
        top:18px;
        font-size: 10px;
        color: #79242F;
    }
  }
  .img_box{
      .img_div{
          width: 380px;
          float: left;
          margin-bottom: 10px;
          margin-right: 15px;
          text-align: center;
           .car_img{
                width: 380px;
                 background: #000;
           }
          img{
              max-width: 100%;
               height: 400px;
               display: block;
               display: inline-block;
               object-fit: contain;
          }
      }
      div:nth-child(3n){
          margin-right: 0;
      }
      .text-bottom{
          height: 40px;
         background: #EFCAA9;
         line-height: 40px;
         text-align: center;
         font-size: 18px;
        color: #79242F;
      }
  }
</style>


